$max-width: 1200px; 
$base-bg: #fff; 
$base-font-size: 16px; 
$base-line-height: 1.4; 
$base-font-family: Arial, sans-serif; 
$custom-font-family: OpenSans, Helvetica, Arial, sans-serif;
$custom2-font-family: Helvetica, Arial, sans-serif;
$link-color: #9b7b4e;
$link-hover-color: #ad844b;

