@mixin placeholder {
    &.placeholder {
        @content;
    }
    &:-moz-placeholder {
        @content;
    }
    &::-moz-placeholder {
        @content;
    }
    &:-ms-input-placeholder {
        @content;
    }
    &::-webkit-input-placeholder {
        @content;
    }
}

@mixin text-decoration {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 0%;
    content: '.';
    color: transparent;
    background: #989b9d;
    height: 1px;
    transition: all 0.4s;
}

* {
    box-sizing: border-box;
}


/* reset */

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
}

ul {
	margin-top: 0;
}


/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
    display: block;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-width: 0px;
    padding: 0px;
    margin: 0px;
}

html {
    height: auto;
	font-size: 100%;
	font-size: 1rem;
	line-height: $base-line-height;
    -webkit-text-size-adjust: none;
    -ms-text-size-adjust: none;
}

body {
	color: $text-color;
    margin: 0px;
    padding: 0px;
    font-family: $base-font-family;
    background-color: $base-bg;
    font-size: $base-font-size;
    height: 100%;
    line-height: $base-line-height;
    position: relative;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

input,
textarea {
    color: #333;
    font-family: $base-font-family;
    outline: none;
    border-radius: 0;
    -moz-border-radius: 0;
    -webkit-border-radius: 0;
}

input[type="button"],
input[type="submit"],
button {
    cursor: pointer
}

td {
    margin: 0px;
    padding: 0px;
}

form {
    padding: 0px;
    margin: 0px;
}

a {
    color: $link-color;
    text-decoration: none;
    outline: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a,
input[type=submit],
input[type=button],
button {
    -webkit-appearance: none;
    outline: none!important;
}

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

*:before,
*:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.clearfix:after,
.wrapper:after,
.row:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
}

a {
    text-decoration: none;
    color: $link-color;
    &:visited, &:hover {
        text-decoration: underline;
        color: $link-color;
    }
    &:active,
    &:focus {
        outline: none;
    }
    &:focus {
        text-decoration: none;
    }
    &:hover {
        color: $link-hover-color;
    }
}

a:active {
    background-color: transparent;
}

textarea,
input[type=url],
input[type=search],
input[type=number],
input[type=password],
input[type=tel],
input[type=text],
input[type=email] {
    height: 32px;
    border: 1px solid #ccc;
    background-color: rgba(255, 255, 255, .2);
    margin: 0;
    font: 16px/30px $base-font-family;
    color: #333;
    padding: 6px 6px;
    vertical-align: middle;
    display: inline-block;
    @include placeholder {
        color: #c7c7c7;
    }
}

textarea {
    width: 222px;
    height: 102px;
    font: 16px/20px $base-font-family;
	overflow: auto;
}

button,
.button,
input[type=button],
input[type=submit] {
    height: 38px;
    border: none;
    background-color: #ccc;
    font: 18px $base-font-family;
    line-height: 38px;
    transition: .2s;
    margin: 0;
    padding: 0 32px;
    color: #fff;
    vertical-align: middle;
    display: inline-block;
    cursor: pointer;
    &:hover {
        background-color: #999;
        color: #fff;
        text-decoration: none;
    }
}

::-webkit-input-placeholder {
    opacity: 1;
}

:-ms-input-placeholder {
    opacity: 1;
}

::-moz-placeholder {
    opacity: 1;
}

:focus::-webkit-input-placeholder {
    color: transparent !important;
}

:focus:-ms-input-placeholder {
    color: transparent !important;
}

:focus::-moz-placeholder {
    color: transparent !important;
}

textarea:invalid,
input:invalid {
    box-shadow: none;
}

body input:focus:required:invalid,
body textarea:focus:required:invalid {
    //color: red;
}

body input:required:valid,
body textarea:required:valid {
    //color: green;
}

.hidden {
    display: none;
}

form {
    margin: 0;
    padding: 0;
}

img {
    border-style: none;
    height: auto;
    max-width: 100%;
}

.hidden {
    display: none;
}

.pull-left {
    float: left;
    margin: 0 11px 3px 0;
}

.pull-right {
    float: right;
    margin-left: 9px;
    margin: 0 0 3px 11px;
}

//  wp
.alignleft {
    float: left;
    margin: 0 11px 3px 0;
}

.alignright {
    float: right;
    margin: 0 0 3px 11px;
}

.aligncenter {
    display: block;
    margin: 0 auto 22px;
}



/* text position */

.text-left {
	text-align: left !important
}

.text-center {
	text-align: center !important
}

.text-right {
	text-align: right !important
}

.nowrap {
	white-space: nowrap !important;
}